body {
  background-color: lightcoral;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 20px;
}

#movie {
  padding: 30px;
  border: 5px solid black;
}

img {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

ul {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin-left: auto;
  margin-right: auto;
}

#flashes {
  font-size: 20px;
  color: red;
}

#comment_list {
  padding: 30px;
  border: 5px solid black;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.comment {
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding: 15px;
  border: 3px gray;
}

#add_comments {
  display: block;
  margin-left: auto;
  margin-right: auto;    
  padding: 20px;
  border: 5px solid gray;
  border-radius: 30px;
}

#logout {
  float: right;
}